import React from 'react';
import AddTodo from './AddTodo';
// import Header from './MyComponents/Header.js'
// import App from './App.js'
import Single_Todo from './Single_Todo'

function Todolist(props){
    console.log(props.todolist)
    return (
        <>
        {props.todolist.length===1?  <Single_Todo key={props.todolist[0].id} addTodo_to_list={props.addfun} todo={props.todolist[0]} onDelete={()=>{}}/> :
        props.todolist.map((todo)=>{
            return <Single_Todo key={todo.id} addTodo_to_list={props.addfun} todo={todo} onDelete={props.onDelete}/>
        })
        }
        </>
    )
}

export default Todolist;